body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: Noto Sans TC;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0em;
  text-align: left;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.card {
  width: 80vw;
  max-width: 600px;
  height: 89vh;
  box-shadow: 0 0 3px 1px rgba(51, 51, 51, 0.5);
  background: #fff;
  border-radius: 8px;
  line-height: 1.4;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
  gap: 12px;
}

.quake {
  animation: queke 0.1s infinite;
}

@keyframes queke {
  0% {
    transform: translateX(50px);
  }
  50% {
    transform: translateX(0px);
  }
}

.old-date,
.start-date {
  width: 80%;
}

input {
  width: 100%;
  height: 4vh;
  background: #f7f8f9;
  border-radius: 8px;
  border: 2px solid #e8ecf4;
}

input:focus {
  border: 2px solid #2e7cf6;
}

.hint {
  color: #f00;
}

.change-btn {
  width: 80%;
  height: 5vh;
  border-radius: 8px;
  background: #e5613b;
  border: none;
  outline: none;
}

.change-btn:active {
  background: #ce5735;
}

.submit-btn {
  width: 80%;
  height: 5vh;
  border-radius: 8px;
  background: #e5613b;
  border: none;
  outline: none;
  margin-top: 8px;
}

.submit-btn:active {
  background: #ce5735;
}
.mask {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: #000000;
  opacity: 0.3;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
}

.popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 30vw;
  padding: 6.2968vh 5.8666vw 4.4977vh;
  background-color: #ffffff;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.3);
  border-radius: 8px;
  opacity: 1;
  z-index: 1000;
}
